.pagination-container {
  display: flex;
  justify-content: center;
  list-style-type: none;

  .pagination-item {
    width: 5rem;
    height: 5rem;
    display: flex;
    font-weight: 700;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: auto 4px;
    color: #e0e0e0;
    display: flex;
    box-sizing: border-box;
    align-items: center;
    transition: all 0.2s ease-out;
    letter-spacing: 0.01071em;
    border-radius: 50%;
    font-size: 13px;

    &.dots:hover {
      background-color: transparent;
      cursor: default;
    }
    &:hover {
      background-color: #231d54;
      cursor: pointer;
    }

    &.selected {
      background-color: #231d54;
    }

    .arrow {
      &::before {
        position: relative;
        /* top: 3pt; Uncomment this to lower the icons as requested in comments*/
        content: '';
        /* By using an em scale, the arrows will size with the font */
        display: inline-block;
        width: 0.4em;
        height: 0.4em;
        border-right: 0.12em solid #e0e0e0;
        border-top: 0.12em solid #e0e0e0;
      }

      &.left {
        transform: rotate(-135deg) translate(-50%);
      }

      &.right {
        transform: rotate(45deg);
      }
    }

    &.disabled {
      pointer-events: none;

      .arrow::before {
        border-right: 0.12em solid #e0e0e0;
        border-top: 0.12em solid #e0e0e0;
      }

      &:hover {
        background-color: #e0e0e0;
        cursor: default;
      }
    }
  }
}
