html,
body,
#root {
  font-size: 10px;
  font-family: 'Orbitron', sans-serif;
  margin: 0;
  padding: 0;
  letter-spacing: 1.8px;
  background-color: #0f0a18;
  overflow: overlay;
}

@media screen and (max-width: 768px) {
  html,
  body,
  #root {
    font-size: 8px !important;
  }
}

@media screen and (max-width: 576px) {
  html,
  body,
  #root {
    font-size: 7px !important;
  }
}

.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 200ms, transform 200ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 200ms, transform 200ms;
}

.fadeslow-enter {
  opacity: 0;
}
.fadeslow-enter-active {
  opacity: 1;
  transition: opacity 2500ms, transform 2500ms;
}
.fadeslow-exit {
  opacity: 1;
}
.fadeslow-exit-active {
  opacity: 0;
  transition: opacity 2500ms, transform 2500ms;
}

.menu-primary-enter {
  opacity: 0;
}

.menu-primary-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

.menu-primary-exit {
  opacity: 1;
}

.menu-primary-exit-active {
  opacity: 0;
  transition: opacity 250ms ease-in;
}

.page {
  position: absolute;
  left: 15px;
  right: 15px;
}

.page-enter {
  opacity: 0;
  transform: scale(0.8);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.3s, transform 0.3s;
}

.page-exit {
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.3s, transform 0.3s;
}

.page-exit-active {
  opacity: 0;
  transform: scale(0.8);
  transition: opacity 0.3s, transform 0.3s;
}

// Custom for react-slick
